/* Rules Button Animation */
.rules-button {
  position: absolute; /* Меняем с fixed на absolute */
  top: -2rem;
  right: -1.4rem;
  background: linear-gradient(135deg, #818cf8 0%, #6366f1 100%);
  color: white;
  font-weight: 500;
  font-size: 0.9rem;
  padding: 0.65rem 1.25rem;
  border-radius: 12px;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 6px -1px rgba(99, 102, 241, 0.15), 
              0 2px 4px -1px rgba(99, 102, 241, 0.08);
  transform: translateY(0);
  outline: none;
  z-index: 50;
}

/* Media queries для размера кнопки на мобильных */
@media screen and (max-width: 767px) {
