  line-height: 1.6 !important;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideIn {
  from {
    transform: translateY(-20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

/* Modal Close Button */
.modal-close {
  position: absolute !important;
  top: 1rem !important;
  right: 1rem !important;
  padding: 0.5rem !important;
