/* EGEFireworks */
.EGEfirework {
  position: absolute;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  animation: EGEfirework-animation 1.7s ease-out infinite;
  opacity: 0.89;
}

/* Animation Keyframes */
@keyframes appear {
  from {
    transform: scale(0.8);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes slideDown {
  from {
    transform: translateY(-20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes bounceIn {
  0% {
    transform: scale(0.3);
    opacity: 0;
  }
  50% {
    transform: scale(1.1);
    opacity: 0.8;
  }
  80% {
    transform: scale(0.9);
    opacity: 0.9;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes EGEfirework-animation {
  0% {
    transform: translate(0, 0);
    width: 11px;
    height: 11px;
    opacity: 0.85;
  }
  50% {
    width: 4px;
    height: 4px;
    opacity: 0.6;
  }
  100% {
    width: 2px;
    height: 2px;
    opacity: 0;
    transform: translate(var(--x), var(--y));
  }
}

/* EGEFirework colors */
.EGEfirework:nth-child(1) { background: rgba(255, 215, 0, 0.9); --x: 50px; --y: -80px; }
.EGEfirework:nth-child(2) { background: rgba(255, 107, 107, 0.85); --x: -30px; --y: -100px; }
.EGEfirework:nth-child(3) { background: rgba(78, 205, 196, 0.85); --x: 70px; --y: -60px; }
