.EGEfirework:nth-child(5) { background: rgba(150, 206, 180, 0.85); --x: 40px; --y: -120px; }
.EGEfirework:nth-child(6) { background: rgba(255, 153, 153, 0.85); --x: -70px; --y: -70px; }

