    padding: 1rem;
    border-radius: 1rem 1rem 0 0;
    z-index: 1000;
    max-height: 90vh;
    overflow-y: auto;
    transform: translateY(0);
    transition: transform 0.3s ease;
    -webkit-transform: translate3d(0,0,0);
    /* Предотвращает дергание на iOS */
    -webkit-overflow-scrolling: touch;
  }

  /* Специальные стили для iPhone Mini и других узких экранов */
  @media screen and (max-width: 375px) {
    .mobile-contact-content {
      /* Уменьшаем максимальную высоту для маленьких экранов */
      max-height: 85vh;
      /* Добавляем дополнительную поддержку для iOS */
      position: fixed;
      height: auto;
      /* Исправляем проблему с transform на iOS */
      transform: translate3d(0,0,0);
      backface-visibility: hidden;
      perspective: 1000;
      /* Оптимизация производительности */
      will-change: transform;
    }

    /* Когда клавиатура активна */
    .mobile-contact-content.keyboard-visible {
      /* Принудительно поднимаем контент выше клавиатуры */
      bottom: auto;
      top: 0;
      max-height: 100%;
